<template>
    <div id="app">
        <div class="out">
            <div class="door">
                <!-- <img src="./bird.gif" alt=""> -->
                <div :class="`door-left ${doorMove} ${doorOpen}`">
                    <img src="./bg1.jpg" alt="">
                </div>
            </div>
        </div>
        <div class="content">
            <svg v-if="svgShow" class="text-svg">
                <text x="50%" y="50" text-anchor="middle"> 七夕节快乐 </text>
            </svg>
            <img src="./bg.jpeg" alt="" :class="`bg ${bgShow && 'active'}`">
            <div :class="`verse-content ${verseShow && 'active'} ${(playing2 || playing) && 'light'}`">
                <div><span>金风玉露一相逢</span></div>
                <div><span>便胜却人间无数</span></div>
                <div><span>两情若是久长时</span></div>
                <div><span>又岂在朝朝暮暮</span></div>
            </div>
            <div class="leaf" v-if="bgShow">
                <div><img src="./leaf4.png" /></div>
                <div><img src="./leaf5.png" /></div>
                <div><img src="./leaf6.png" /></div>
            </div>
            <div :class="`music-btn ${!playing && 'active'}`" id="musicBtn" @click="onPlay">
                <img  src="./music.png" alt="">
            </div>
            <div v-if="bgShow" class="music2" @click="onPlay2"><span>❤️</span></div>
        </div>
        <audio id="audio" src="http://119.23.225.147:8888/bgmusic.3a848436.mp3" loop></audio>
        <audio id="audio1" src="http://119.23.225.147:8888/like.22f46a21.mp3" loop></audio>
    </div>
</template>

<script>
// const a = require('./bgmusic.mp3');
// const b = require('./like.mp3');
    
export default {
    name: 'App',
    components: {
    },
    data() {
        return {
            doorMove: '',
            doorOpen: '',
            svgShow: false,
            bgShow: false,
            verseShow: false,
            
            audio: '',
            audio1: '',
            // oneAudio: a,
            // twoAudio: b,
            playing: false,
            timer: 0,
            rotate: 0,
            
            playing2: false,
        };
    },
    methods: {
        onPlay() {
            if (this.playing) {
                this.audio.pause();
                clearInterval(this.timer);
            } else {
                this.audio.play();
                this.setRotate();
                this.audio1.pause();
                this.playing2 = false;
            }
            this.playing = !this.playing;
        },
        
        onPlay2() {
            if (this.playing2) {
                this.audio1.pause();
            } else {
                this.audio1.play();
                this.audio.pause();
                clearInterval(this.timer);
                this.playing = false;
            }
            this.playing2 = !this.playing2;
        },
        
        openDoor() {
            setTimeout(() => {
                this.doorOpen = 'active';
                this.doorMove = '';
                this.showVerse();
            }, 2500)
        },
        showSvg() {
            setTimeout(() => {
                this.svgShow = true;
            }, 4500)
        },
        showVerse() {
            setTimeout(() => {
                this.verseShow = true;
                this.showSvg();
            }, 700);
        },
        
        setRotate() {
            this.timer = setInterval(() => {
                this.rotate += 72;
                if (this.rotate >= 36000) this.rotate = 0;
                document.getElementById('musicBtn').style.transform = `rotate(${this.rotate / 100}deg)`;
            }, 20);
        },
    },
    mounted() {
        this.$nextTick(() => {
            setTimeout(() => {
                this.doorMove = 'move';
                this.openDoor();
                setTimeout(() => {
                    this.bgShow = true;
                }, 2600);
            }, 0)
            this.audio = document.getElementById('audio');
            this.audio1 = document.getElementById('audio1');
        })
    }
}
</script>

